// Display flex on the image and the header so that they are side by side. 
.headerParent {
  display: flex;
  justify-content: center;
}

// The small image to the left of the header
img {
  width: 126px;
  margin: 0 13px 0 0;
  height: 160px; 
}

// The header for the app
h1 {
  font-family: 'Oswald', sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 90px;
  margin: 0 0 21px 0;
  padding-top: 36px;
  color: $darkFont;
  letter-spacing: 2px;
  span {
    font-weight: 300;
  }
}

p {
  font-size: 22px;
  font-family: 'Montserrat';
  font-weight: 300;
  text-transform: uppercase;
  span {
    font-weight: 600;
    font-size: 22px;
  }
}

.data > header:nth-child(1) > p:nth-child(2) {
  margin-top: 22px;
  letter-spacing: 1px;
}